.sugerenciaAmigo{
    display: flex;
    flex-direction: row; 
    overflow-x: auto;
}
.persona{
    background-color: $gris;
    margin: 0 1rem 1rem 0;
    border-radius: 1rem;
}
.personaPic img{border-radius: 1rem 1rem 0 0;}
.personaBotones{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.btnPersona{margin: 0 1rem 1rem 1rem 1rem;}
.agregarAmigo button{
    background-color: rgb(38, 57, 81);
    color: rgb(43, 134, 255);
    border-radius: 1rem;
    display: flex;
    align-items: center;
}
.agregarAmigo svg{
    stroke: rgb(43, 134, 255);
    margin-right: .5rem;
    width: 30px;
    height: 30px;
}
.eliminarsugerencia button{
    border-radius: 1rem;
    width: auto;
    height: 30px;
}
.eliminarsugerencia button, .agregarAmigo button{
    cursor: pointer;
    border: none;
}
