.header{
    background-color: $grisClaro; 
    padding: 1rem 0;
    @include desktop{display: none;}
}
.header-contenido{
    display: grid;
    grid-template-columns: 10% 80% 10%;
    align-items: center;
    justify-items:center;
}
.buscadorHeader{
    input{
        text-align: center;
        background-color: transparent;
        border: none;
        border-bottom: solid 2px #fff;
        color: $grisFont;
    }
}
.navegador-principal{
    padding: 1rem 0; 
    border-bottom: solid 1px $grisClaro;
    @include desktop{display: none;}
}
.nav-principal{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    align-items: center;
    justify-items: center;
    svg{width: 28px; height: 28px;}
}

//=================== BUSCADOR ===================
.feed{
    display: grid;
    grid-template-columns: 10% 80% 10%;
    justify-items: center;
    align-items: center;
    padding: 1rem;
    @include desktop{display: none;}
}
.feedImgProfile img{
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
}
.feedInput{width: 86%;}
.feedInput input{
    border:none;
    border-radius: 10rem;    
    width: 100%;
    padding: .6rem 0;
    text-align: center;
    color: $gris;
}
.feedFoto{p{margin: 0; color: $grisFont}}

//============== SEGUNDO NAVEGADOR
.buscador-Navegador{
    display: none;
    background-color: $grisClaro;
    @include desktop{
        display: grid;
        grid-template-columns: 29% 42% 29%;
        height: auto;
        padding: .5rem 1rem;
        margin: 0 0 2rem 0;
    }
}
.segundoBuscador{
    display: flex;
    align-items: center;
    input{
        border: none;
        background-color: $grisFont;
        border-radius: 5rem;
        padding: 0 1rem;
        height: 3rem;
        width: auto;
    }
    svg{width: 40px;}
}
.segundoNavPrincipal{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    align-items: center;
    justify-items: center;
    svg{width: 40px; height: 40px;}
    
}
.segundoNavPrincipal-icon:hover{
    background-color: rgba($grisFont, .3);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 1rem;
}
.opcionesSesion{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.opcionesSesion-usuario{
    display: flex;
    align-items: center;
    margin-right: 2rem;
    img{width: 3.5rem; border-radius: 5rem; margin-right: 1rem;}
}
.opcionesSesion-icons{
    display: flex;
    align-items: center;
    svg{width: 30px; height: 30px;}
}
.opcionesSesion-icon{
    background-color: $grisOscuro;
    border-radius: 50%;
    margin-right:1rem;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover{background-color: rgba($grisFont, .3);}
}
.here svg{
    stroke: $azul;
    border-bottom: solid .3rem $azul
}