body {
  font-family: Arial;
  font-size: 1.6rem;
  color: #afb3b8; }

.container {
  width: 95%;
  margin: 0 auto; }
  @media (min-width: 1200px) {
    .container {
      margin: 5rem auto 0 auto; } }

h1, h2, h3, h4, h5 {
  margin: 0; }

img {
  width: 100%; }

a {
  text-decoration: none; }

@media (min-width: 1200px) {
  .mainNavegadorSecundario {
    display: grid;
    grid-template-columns: 29% 42% 29%; } }

.nav-secundario {
  display: none; }
  @media (min-width: 1200px) {
    .nav-secundario {
      display: flex;
      flex-direction: column; }
      .nav-secundario a {
        color: #afb3b8;
        display: flex;
        align-items: center;
        padding: .5rem .5rem;
        margin-bottom: 1rem;
        width: 30rem; }
        .nav-secundario a:hover {
          background-color: #242526;
          border-radius: 1rem; }
      .nav-secundario img {
        width: 4rem;
        height: 4rem;
        border-radius: 5rem; }
      .nav-secundario svg, .nav-secundario img {
        margin-right: 2rem; } }

.objetoFijado {
  position: fixed;
  width: 20rem; }

.fijarNav {
  position: fixed;
  width: 100%;
  padding: 10rem;
  top: 0; }

.posts {
  background-color: #3a3b3c;
  width: 100%;
  margin-bottom: 1.25rem;
  border-radius: 1rem; }

.title {
  padding: 1rem 0; }

.header {
  background-color: #3a3b3c;
  padding: 1rem 0; }
  @media (min-width: 1200px) {
    .header {
      display: none; } }

.header-contenido {
  display: grid;
  grid-template-columns: 10% 80% 10%;
  align-items: center;
  justify-items: center; }

.buscadorHeader input {
  text-align: center;
  background-color: transparent;
  border: none;
  border-bottom: solid 2px #fff;
  color: #afb3b8; }

.navegador-principal {
  padding: 1rem 0;
  border-bottom: solid 1px #3a3b3c; }
  @media (min-width: 1200px) {
    .navegador-principal {
      display: none; } }

.nav-principal {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  align-items: center;
  justify-items: center; }
  .nav-principal svg {
    width: 28px;
    height: 28px; }

.feed {
  display: grid;
  grid-template-columns: 10% 80% 10%;
  justify-items: center;
  align-items: center;
  padding: 1rem; }
  @media (min-width: 1200px) {
    .feed {
      display: none; } }

.feedImgProfile img {
  width: 5rem;
  height: 5rem;
  border-radius: 50%; }

.feedInput {
  width: 86%; }

.feedInput input {
  border: none;
  border-radius: 10rem;
  width: 100%;
  padding: .6rem 0;
  text-align: center;
  color: #242526; }

.feedFoto p {
  margin: 0;
  color: #afb3b8; }

.buscador-Navegador {
  display: none;
  background-color: #3a3b3c; }
  @media (min-width: 1200px) {
    .buscador-Navegador {
      display: grid;
      grid-template-columns: 29% 42% 29%;
      height: auto;
      padding: .5rem 1rem;
      margin: 0 0 2rem 0; } }

.segundoBuscador {
  display: flex;
  align-items: center; }
  .segundoBuscador input {
    border: none;
    background-color: #afb3b8;
    border-radius: 5rem;
    padding: 0 1rem;
    height: 3rem;
    width: auto; }
  .segundoBuscador svg {
    width: 40px; }

.segundoNavPrincipal {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-items: center;
  justify-items: center; }
  .segundoNavPrincipal svg {
    width: 40px;
    height: 40px; }

.segundoNavPrincipal-icon:hover {
  background-color: rgba(175, 179, 184, 0.3);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem; }

.opcionesSesion {
  display: flex;
  align-items: center;
  justify-content: flex-end; }

.opcionesSesion-usuario {
  display: flex;
  align-items: center;
  margin-right: 2rem; }
  .opcionesSesion-usuario img {
    width: 3.5rem;
    border-radius: 5rem;
    margin-right: 1rem; }

.opcionesSesion-icons {
  display: flex;
  align-items: center; }
  .opcionesSesion-icons svg {
    width: 30px;
    height: 30px; }

.opcionesSesion-icon {
  background-color: #18191a;
  border-radius: 50%;
  margin-right: 1rem;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center; }
  .opcionesSesion-icon:hover {
    background-color: rgba(175, 179, 184, 0.3); }

.here svg {
  stroke: #0b86ee;
  border-bottom: solid 0.3rem #0b86ee; }

.stories-imagenes {
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  overflow-y: hidden;
  padding: 0; }

.historiaPic img {
  width: auto;
  height: 15rem;
  border-radius: 1rem;
  cursor: pointer;
  margin-right: 1rem; }
  @media (min-width: 768px) {
    .historiaPic img {
      height: 20rem; } }

.agregarStorie p {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 2rem;
  height: 2rem;
  background-color: #fff;
  color: blue;
  margin: 1rem 0 0 -8.8rem;
  border-radius: 1rem; }
  @media (min-width: 768px) {
    .agregarStorie p {
      margin: 1rem 0 0 -11rem; } }

.divDescript p {
  color: #fff;
  font-size: 1.3rem;
  margin: 13rem 0 0 -8rem; }
  @media (min-width: 768px) {
    .divDescript p {
      margin: 17.5rem 0 0 -9rem; } }

.sugerenciaAmigo {
  display: flex;
  flex-direction: row;
  overflow-x: auto; }

.persona {
  background-color: #242526;
  margin: 0 1rem 1rem 0;
  border-radius: 1rem; }

.personaPic img {
  border-radius: 1rem 1rem 0 0; }

.personaBotones {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; }

.btnPersona {
  margin: 0 1rem 1rem 1rem 1rem; }

.agregarAmigo button {
  background-color: #263951;
  color: #2b86ff;
  border-radius: 1rem;
  display: flex;
  align-items: center; }

.agregarAmigo svg {
  stroke: #2b86ff;
  margin-right: .5rem;
  width: 30px;
  height: 30px; }

.eliminarsugerencia button {
  border-radius: 1rem;
  width: auto;
  height: 30px; }

.eliminarsugerencia button, .agregarAmigo button {
  cursor: pointer;
  border: none; }

.usuarioPost {
  display: flex;
  padding: 1rem 0; }
  .usuarioPost a {
    color: #fff;
    font-weight: bold; }
  .usuarioPost p {
    margin: 0;
    display: flex;
    align-items: center;
    gap: 1rem; }
  .usuarioPost svg {
    width: 15px; }
  .usuarioPost img {
    width: 5rem;
    height: 5rem;
    border-radius: 5rem;
    margin-right: 1rem; }

.postContenido {
  text-align: center;
  padding: 1rem 0;
  border-bottom: solid 0.1rem #242526; }

.BtnseccionReacciones {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0; }
  .BtnseccionReacciones button {
    cursor: pointer;
    margin-right: 1rem;
    background-color: transparent;
    color: #afb3b8;
    border-radius: 1rem;
    display: flex;
    align-items: center; }
  .BtnseccionReacciones button:hover {
    background-color: #18191a; }
  .BtnseccionReacciones svg {
    margin-right: .3rem; }

.like button {
  padding: 0 1rem; }

.conteoReacciones {
  display: flex;
  justify-content: space-between;
  border-bottom: solid 0.1rem #242526; }

.conteoReacciones-likes {
  display: flex;
  align-items: center; }
  .conteoReacciones-likes svg {
    width: 25px;
    margin-right: .5rem; }

.disteLike button {
  color: #2374e1;
  border-color: #2374e1; }
  .disteLike button svg {
    stroke: #2374e1; }
