.stories-imagenes {
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    overflow-y: hidden;
    padding: 0;
}

.historiaPic img {
    width: auto;
    height: 15rem;
    border-radius: 1rem;
    cursor: pointer;
    margin-right: 1rem;
    @include tablet{
        height: 20rem;
    }
}
.agregarStorie p{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 2rem;
    height: 2rem;
    background-color: $blanco;
    color: blue;
    margin: 1rem 0 0 -8.8rem;
    border-radius: 1rem;
    @include tablet{margin: 1rem 0 0 -11rem;}
}
.divDescript p{
    color: $blanco;
    font-size: 1.3rem;
    margin: 13rem 0 0 -8rem;
    @include tablet{
        margin: 17.5rem 0 0 -9rem;
    }
}
