/* apply a natural box layout model to all elements, but allowing components to change */
html {
    box-sizing: border-box;
    font-size: 62.5%;
    background-color: $grisOscuro;
  }
  *, *:before, *:after {
    box-sizing: inherit;
}
body{
    font-family: Arial;
    font-size: 1.6rem;
    color: $grisFont;
}
.container{
    width: 95%;
    margin: 0 auto;
    @include desktop{margin: 5rem auto 0 auto;}
}
h1,h2,h3,h4,h5{margin: 0;}
img{width: 100%;}
a{text-decoration: none;}

.mainNavegadorSecundario{
    @include desktop{
        display: grid;
        grid-template-columns: 29% 42% 29%;
    }
}
.nav-secundario{
    display: none;
    @include desktop{
        display: flex;
        flex-direction: column;
        a{
            color: $grisFont;
            display: flex;
            align-items: center;
            padding: .5rem .5rem;
            margin-bottom: 1rem;
            width: 30rem;
            &:hover{background-color: $gris; border-radius: 1rem;}
        }
        img{
            width: 4rem; 
            height: 4rem; 
            border-radius: 5rem;
        }
        svg,img{margin-right: 2rem;}
    } 
}
.objetoFijado{position: fixed; width: 20rem;}
.fijarNav{position: fixed; width: 100%; padding: 10rem; top: 0;}

