.usuarioPost{
    display: flex;;
    padding: 1rem 0;
    a{
        color: $blanco;
        font-weight: bold;
    }
    p{
        margin: 0;
        display: flex;
        align-items: center;
        gap: 1rem;
    }
    svg{width: 15px;}
    img{
        width: 5rem;
        height: 5rem;
        border-radius: 5rem;
        margin-right: 1rem;
    }
}
.postContenido{
    text-align: center;
    padding: 1rem 0;
    border-bottom: solid .1rem $gris;
}
.BtnseccionReacciones{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;
    button{
        cursor: pointer; 
        margin-right: 1rem;
        background-color: transparent;
        color: $grisFont;
        border-radius: 1rem;
        display: flex;
        align-items: center;
    }
    button:hover{background-color: $grisOscuro;}
    svg{margin-right: .3rem;}
}
.like button{
    padding: 0 1rem;
}
.conteoReacciones{
    display: flex;
    justify-content: space-between;
    border-bottom: solid .1rem $gris;
}
.conteoReacciones-likes{
    display: flex;
    align-items: center;
    svg{width: 25px; margin-right: .5rem;}
}

.disteLike button{
    color: $azulFuerte;
    border-color: $azulFuerte;
    svg{stroke: $azulFuerte;}
}